import React, { useEffect, useState, useCallback } from 'react';
import { ChakraProvider, Box, theme, extendTheme } from '@chakra-ui/react';
import { useDispatch, useSelector } from 'react-redux';
import { ToastContainer, toast } from 'react-toastify';
import WalletConnectProvider from '@walletconnect/web3-provider';
import CoinbaseWalletSDK from '@coinbase/wallet-sdk';
import { providers, Contract, utils as etherUtils } from 'ethers';
import Web3 from "web3"
import WalletLink from 'walletlink';
import Web3Modal from 'web3modal';
import axios from 'axios';
import 'react-toastify/dist/ReactToastify.css';
import '@fontsource/roboto/400.css';
import '@fontsource/roboto/700.css';
import '@fontsource/montserrat/800.css';
import '@fontsource/oleo-script-swash-caps/400.css';

import GameModel from './pages/GameModel';
import Fonts from './Fonts';
import { Home, Gallery, Roadmap, About, Team, Features, FAQ } from './pages';
import { Header, Footer } from './components';
import Metamon from './pages/Metamon';
import Others from './pages/Others';
import GameSneakPeek from './pages/GameSneakPeek';
import { connectRequest, connectSuccess, connectFailed, updateAccountRequest, resetRequest } from './redux/blockchain/blockchainActions';
import { fetchDataRequest, fetchDataSuccess, fetchDataFailed } from './redux/data/dataActions';
import ContractAbi from './contracts/ABI.json';

import MerkleTree from 'merkletreejs'
import keccak256 from 'keccak256'

import whiteListAddresses from './addresses.json'


const extendedTheme = extendTheme({
  ...theme,
  fonts: {
    heading: 'Montserrat',
    body: 'Roboto',
    fancy: 'Oleo Script Swash Caps',
  },
  colors: {
    ...theme.colors,
    bg: '#241c31',
    bgLight: '#efeee8',
    bgDark: '#e8e7df',
    lightBlack: '#181817',
    lightOrange: '#feb64d',
    bdgrey: '#505450',
    bdgreyop: '#50545025',
    yellow: '#ffbb28',
    bg3: '#0f1111',
    bg4: '#dc3545',
    textColor: '#1a191b',
  },
});


const {
  REACT_APP_CONTRACT_ADDRESS,
  REACT_APP_INFURA_ID,
  REACT_APP_NETWORK_ID,
  REACT_APP_NETWORK,
  REACT_APP_WHITELIST_API,
  REACT_APP_MERKEL_TREE_API,
} = process.env;

const config={
  contractAddress:"0xa74eE803Aa01B03afaDEb5fBDc9cC1491932Dcfb",
  networkId:"1",
  network:"ethereum",
}



const providerOptions = {
  walletconnect: {
    package: WalletConnectProvider, // required
    options: {
      infuraId: REACT_APP_INFURA_ID, // required
    },
  },
  coinbasewallet: {
    package: CoinbaseWalletSDK, // Required
    options: {
      appName: "Metamon", // Required
      infuraId: REACT_APP_INFURA_ID, // Required
      rpc: "", // Optional if `infuraId` is provided; otherwise it's required
      chainId: 3, // Optional. It defaults to 1 if not provided
      darkMode: false // Optional. Use dark theme, defaults to false
    }
  }
};

const web3Modal = new Web3Modal({
  cacheProvider: true,
  providerOptions, // required
});

function App() {
  const [whitelist, setwhitelist] = useState([]);
  const [fetching, setFetching] = useState(false);
  const blockchain = useSelector(state => state.blockchain);
  const data = useSelector(state => state.data);
  const dispatch = useDispatch();

  const getWhitelistedUsers = async () => {
    try {
      setFetching(true);
      const response = await axios.get(REACT_APP_WHITELIST_API);
      const data = response.data.data;
      setwhitelist(data);
      setFetching(false);
    } catch (error) {
      console.log(error);
      setFetching(false);
    }
  };

  useEffect(() => {
    getWhitelistedUsers();
  }, []);

  const connect = useCallback(async () => {
    try {
      dispatch(connectRequest());
      const provider = await web3Modal.connect();
      // const web3 = new providers.Web3Provider(provider);
      const web3 = new Web3(provider);
      // const signer = web3.getSigner();
      const address = await web3.eth.getAccounts();
      const network = await web3.eth.net.getId();
      // const myContract = new web3.eth.Contract(ContractAbi, REACT_APP_CONTRACT_ADDRESS)
      const myContract = new web3.eth.Contract(ContractAbi, config.contractAddress)

      const smartContract = myContract.methods
      //   new Contract(
      //   REACT_APP_CONTRACT_ADDRESS,
      //   ContractAbi,
      //   signer
      // );
      dispatch(
        connectSuccess({
          provider,
          web3,
          address: address[0],
          chainId: network,
          smartContract,
        })
      );
    } catch (err) {
      console.log("err.message");
      // dispatch(connectFailed(err.message));
    }
  }, []);

  const {
    provider,
    web3: web3Provider,
    address,
    chainId,
    smartContract,
    loading: blockchainLoading,
    error: blockchainError,
  } = blockchain;

  useEffect(() => {
    if (smartContract) {
      getData();
    }
  }, [smartContract]);

  const getData = async () => {
    try {
      if (!!smartContract) {
        dispatch(fetchDataRequest());
        let totalSupply = (await smartContract.totalSupply().call());
        // let totalSupply = (await smartContract.totalSupply()).toNumber();
        console.log(totalSupply);
        let cost = await smartContract.cost().call();
        console.log(cost);
        let maxSupply = (await smartContract.maxSupply().call());
        // let maxSupply = (await smartContract.maxSupply()).toNumber();
        console.log(maxSupply);
        let presaleActive = await smartContract.presaleActive().call();
        console.log(presaleActive);
        let saleActive = await smartContract.saleActive().call();
        console.log(saleActive);
        let maxMintAmount = ( await smartContract.maxMintAmountPerTx().call() );
        // let maxMintAmount = ( await smartContract.maxMintAmountPerTx() ).toNumber();
        let ownerAddr = await smartContract.owner().call();
        // const price = etherUtils.formatEther(cost);
        const price = parseInt(cost) / 1000000000000000000;
        dispatch(
          fetchDataSuccess({
            totalSupply,
            maxSupply,
            maxMintAmount,
            cost: price,
            owner: ownerAddr,
            presaleActive,
            saleActive,
          })
        );
      }
    } catch (error) {
      console.log(error);
      dispatch(fetchDataFailed(error.message));
    }
  };

  useEffect(() => {
    if (blockchainError) {
      toast.error(blockchainError);
    }
    if (data.error) {
      toast.error(data.error);
    }
  }, [blockchainError, data.error]);

  const disconnect = useCallback(
    async function () {
      await web3Modal.clearCachedProvider();
      if (provider?.disconnect && typeof provider.disconnect === 'function') {
        await provider.disconnect();
      }
      dispatch(resetRequest());
    },
    [provider]
  );

  useEffect(() => {
    if (web3Modal.cachedProvider) {
      // connect();
    }
  }, [connect]);

  useEffect(() => {
    if (provider?.on) {
      const handleAccountsChanged = accounts => {
        // eslint-disable-next-line no-console
        console.log('accountsChanged', accounts);
        updateAccountRequest({
          address: accounts[0],
        });
        window.location.reload();
      };

      const handleChainChanged = _hexChainId => {
        window.location.reload();
      };

      const handleDisconnect = error => {
        // eslint-disable-next-line no-console
        console.log('disconnect', error);
        disconnect();
      };

      provider.on('accountsChanged', handleAccountsChanged);
      provider.on('chainChanged', handleChainChanged);
      provider.on('disconnect', handleDisconnect);

      // Subscription Cleanup
      return () => {
        if (provider.removeListener) {
          provider.removeListener('accountsChanged', handleAccountsChanged);
          provider.removeListener('chainChanged', handleChainChanged);
          provider.removeListener('disconnect', handleDisconnect);
        }
      };
    }
  }, [provider, disconnect]);

  useEffect(() => {
    let REACT_APP_NETWORK_ID=config.networkId
    let network=config.network
    console.log(chainId, Number(chainId), Number(REACT_APP_NETWORK_ID));
    if (!!chainId && Number(chainId) !== Number(REACT_APP_NETWORK_ID)) {
      toast.error(`Change the network to ${REACT_APP_NETWORK}`, {
        position: 'top-right',
        autoClose: 5000,
        hideProgressBar: true,
        closeOnClick: true,
        pauseOnHover: false,
        draggable: false,
      });
    }
  }, [chainId]);

  const claimNFTs = async count => {
    if (count <= 0) {
      return;
    }

    try {
      toast.info('Preparing your NFT...');
      const value = etherUtils.parseEther(
        (Number(data.cost) * Number(count)).toString()
      );
      console.log(value, Number(data.cost) * Number(count));
      // const estimate = await smartContract.estimateGas.mint(count, {
      //   value,
      //   from: address,
      // });
      const trx = await smartContract.publicMint(count).send({
        value,
        from: address,
      })
      .on('receipt', function(receipt){
        console.log(receipt) // contains the new contract address
        toast.success('Woohoo! NFT minted successfully!');
        getData();
     })
      // const receipt = await trx.wait();
      // console.log(receipt);
      // toast.success('Woohoo! NFT minted successfully!');
      // getData();
    } catch (err) {
      if (err.message.includes('insufficient funds')) {
        toast.error('Insufficient Funds');
      } else if (err.message.includes('Not a valid leaf in the Merkle tree')) {
        toast.error('Not a whitelisted user');
      } else {
        const formatedError = err.message.split('{')[0];
        if (formatedError.includes('execution reverted:')) {
          toast.error(formatedError.split('execution reverted:')[1]);
        } else {
          toast.error(formatedError);
        }
      }
    }
  };
  console.log(blockchain.account)
  useEffect(()=>{
    // alert('new build 2')

  },[])
  const whitelistNFTs = async count => {
    const whiteListAddressesLeaves = whiteListAddresses.map((x) => keccak256(x))
    const merkletree = new MerkleTree(whiteListAddressesLeaves, keccak256 ,{sortPairs:true})
    const merklePoof=merkletree.getHexProof(keccak256(address))
    const rootNode=merkletree.getRoot().toString('hex');
    let whitelisted=merkletree.verify(merklePoof,keccak256(address),rootNode)

    if(whitelisted===false){
      toast.error('Not whitelisted!');
      return;
    }
    if (count <= 0) {
      return;
    }

    // if (whitelist.length === 0) {
    //   toast.error('Something went wrong!');
    //   return;
    // }

    if (!smartContract) {
      toast.error('Something went wrong!');
      return;
    }

    try {
      toast.info('Preparing your NFT...');
      const value = etherUtils.parseEther(
        (Number(data.cost) * Number(count)).toString()
      );
   

      const trx = await smartContract.PresaleMint(merklePoof,count).send({
        from: address,
        value,
        gasLimit:String(150000)
      })
      .on('receipt', function(receipt){
        console.log(receipt) // contains the new contract address
        toast.success('Woohoo! NFT minted successfully!');
        getData();
     })


    } catch (err) {
      if (err.message.includes('insufficient funds')) {
        toast.error('Insufficient Funds');
      } else if (err.message.includes('Not a valid leaf in the Merkle tree')) {
        toast.error('Not a whitelisted user!');
      } else if (err.message.includes('Invalid params')) {
        toast.error('Invalid Data!');
      } else {
        const formatedError = err.message.split('{')[0];
        if (formatedError.includes('execution reverted:')) {
          toast.error(formatedError.split('execution reverted:')[1]);
        } else {
          console.log(formatedError);
          toast.error(formatedError);
        }
      }
    }
  };


  return (
    <ChakraProvider theme={extendedTheme}>
      <Fonts />
      <Box minH="100vh">
        <Header onConnect={connect} address={address} />
        <Box>
          <Home />
          <About
            onConnect={connect}
            address={address}
            data={data}
            loading={blockchainLoading || data.loading || fetching}
            onMint={claimNFTs}
            onWhitelistMint={whitelistNFTs}
          />
          <Gallery />
          <GameSneakPeek />
          <GameModel />
          <Roadmap />
          {/* <Others /> */}
          <Metamon />
          <Features />
          <Team />
          <FAQ />
        </Box>
        <Footer />
        <ToastContainer />
      </Box>
    </ChakraProvider>
  );
}

export default App;
