import React, { useEffect, useState } from 'react';
import { Box, Flex, Image, Link, useDisclosure, Drawer, DrawerOverlay, DrawerContent, DrawerHeader, DrawerBody, Text, Heading } from '@chakra-ui/react';
import { HiMenu } from 'react-icons/hi';

import { shortenAddress } from '../candy-machine';

import { SiDiscord } from 'react-icons/si';
import { AiOutlineTwitter, AiFillInstagram } from 'react-icons/ai';

export default function Header({ address, onConnect }) {
  const { isOpen, onOpen, onClose } = useDisclosure();
  const [placement] = React.useState('right');

  return (
    <>
      <Box className="header">
        <Flex
          maxW="1200px"
          mx="auto"
          justifyContent="space-between"
          alignItems="center"
        >
          {/* <Heading color="white">DUMMY</Heading> */}

          <Link href="/" className="link anam" py="0.3125rem">
            <Image
              ml={{ base: 0, md: '50px' }}
              src={`${process.env.PUBLIC_URL}/llg.png`}
              className="lgimg"
            />
          </Link>

          <Box alignItems="center" display={{ base: 'none', md: 'flex' }}>
            <Link href="#about" className="link" mr="10px" color="white">
              Mint
            </Link>
            <Link href="#gallery" className="link" mr="10px" color="white">
              The Story
            </Link>
            <Link href="#roadmap" className="link" mr="10px" color="white">
              Milestone
            </Link>
            <Link href="#features" className="link" mr="10px" color="white">
              Features
            </Link>

            <Link href="#team" className="link" color="white" mr="10px">
              Team
            </Link>
            <Link href="#faq" className="link" color="white" mr="10px">
              FAQ
            </Link>

            <Link
              href="https://twitter.com/Metamongonft"
              className="social-link"
              isExternal
              color="bgLight"
              mr="10px"
            >
              <AiOutlineTwitter size="14px" />
            </Link>
            <Link
              href="https://discord.com/invite/dwfQ9gJExa/"
              className="social-link"
              isExternal
              color="bgLight"
              mr="10px"
            >
              <SiDiscord size="14px" />
            </Link>
            <Link
              href="https://www.instagram.com/metamongo.io/"
              className="social-link"
              isExternal
              color="bgLight"
              mr="10px"
            >
              <AiFillInstagram size="14px" />
            </Link>
            <Link href="#about" className={`enterRaffle`}>
              MINT
            </Link>

            {!address ? (
              <Flex
                as="button"
                bg="bg4"
                width="120px"
                h="40px"
                color="white"
                borderRadius="5px"
                justifyContent="center"
                alignItems="center"
                className="desktop-btn"
                display={{ base: 'none', lg: 'flex' }}
                onClick={onConnect}
              >
                Connect
              </Flex>
            ) : (
              <Box
                bg="bg4"
                px="20px"
                py="10px"
                borderRadius="5px"
                display={{ base: 'none', lg: 'flex' }}
              >
                <Text color="white">{shortenAddress(address || '')}</Text>
              </Box>
            )}
          </Box>

          <Box
            as="button"
            bg="white"
            p="2"
            onClick={onOpen}
            display={{ base: 'block', md: 'none' }}
          >
            <HiMenu color="black" size="30" />
          </Box>
        </Flex>
      </Box>
      <Drawer
        placement={placement}
        onClose={onClose}
        isOpen={isOpen}
        display={{ base: 'block', md: 'none' }}
      >
        <DrawerOverlay />
        <DrawerContent>
          <DrawerHeader borderBottomWidth="1px">Menu</DrawerHeader>
          <DrawerBody>
            <Link
              href="#about"
              className="link"
              mr="10px"
              color="black"
              display="block"
              py="15px"
            >
              Mint
            </Link>
            <Link
              href="#gallery"
              className="link"
              mr="10px"
              color="black"
              display="block"
              py="15px"
            >
              The Story
            </Link>
            <Link
              href="#roadmap"
              className="link"
              mr="10px"
              color="black"
              display="block"
              py="15px"
            >
              Milestone
            </Link>
            <Link
              href="#features"
              className="link"
              mr="10px"
              color="black"
              display="block"
              py="15px"
            >
              Features
            </Link>

            <Link
              href="#team"
              className="link"
              color="black"
              mr="10px"
              display="block"
              py="15px"
            >
              Team
            </Link>
            <Link
              href="#faq"
              className="link"
              color="black"
              mr="10px"
              display="block"
              py="15px"
            >
              FAQ
            </Link>
            <Flex my="20px">

              <Link
                href="https://twitter.com/Metamongonft"
                className="social-link"
                isExternal
                color="bgLight"
                mr="10px"
              >
                <AiOutlineTwitter size="14px" />
              </Link>
              <Link
                href="https://discord.com/invite/2ZvWHsQzcW"
                className="social-link"
                isExternal
                color="bgLight"
                mr="10px"
              >
                <SiDiscord size="14px" />
              </Link>
              <Link
                href="https://www.instagram.com/metamongo.io/"
                className="social-link"
                isExternal
                color="bgLight"
                mr="10px"
              >
                <AiFillInstagram size="14px" />
              </Link>
            </Flex>
            <Link href="#about" className={`enterRaffle`} w="100%" mb="20px">
              MINT
            </Link>
            {!address ? (
              <Flex
                className="mobile-btn"
                as="button"
                bg="bg4"
                width="200px"
                h="40px"
                color="white"
                borderRadius="5px"
                justifyContent="center"
                alignItems="center"
                onClick={onConnect}
              >
                Connect
              </Flex>
            ) : (
              <Box bg="bg4" px="20px" py="10px" borderRadius="5px">
                <Text color="white">{shortenAddress(address || '')}</Text>
              </Box>
            )}
          </DrawerBody>
        </DrawerContent>
      </Drawer>
    </>
  );
}
